export default {
  // 此处编写的就是 Vue组件实例的配置项，通过一定语法，可以直接混入到组件内部
  // 注意点： 如果此处 和组件内提供了同名的data 或 methods，则组件内优先级更高
  methods: {
    loginConfirm () {
      // 判断 token是否存在
      // 1.如果 token 存在，弹确然框
      // 2. 如果token存在，请继续操作
      if (!this.$store.getters.token) {
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要先登录才能继续操作哦',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // 如果希望，跳转到登录 => 登陆后能跳回来，需要在跳转去携带参数（当前的路径地址）
            // this.$route.fullPath(会包含查询参数)
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => { })
        return true
      }
      return false
    }
  }
}
